/* src/styles/responsive.css */

/* === RESPONSIVE DESIGN === */

/* Màn hình tablet và nhỏ hơn */
@media (max-width: 1024px) {
  .main-content {
    grid-template-columns: 1fr 280px; /* Thu nhỏ sidebar */
  }
  
  .app-header {
    padding: 0 12px;
    height: auto;
    min-height: 60px;
  }
  
  .app-header h1 {
    font-size: 1.2rem;
  }
  
  .header-buttons {
    gap: 8px;
  }
  
  .header-buttons button {
    padding: 6px 12px;
    font-size: 0.85rem;
  }
}

/* Màn hình mobile */
@media (max-width: 768px) {
  .main-content {
    grid-template-columns: 1fr; /* Stack layout: planner chiếm full */
  }
  
  .right-sidebar {
    display: none; /* Ẩn sidebar trên mobile hoặc có thể làm drawer */
  }
  
  .app-header {
    flex-direction: column;
    align-items: flex-start;
    padding: 8px 12px;
    gap: 8px;
  }
  
  .app-header h1 {
    font-size: 1rem;
    width: 100%;
  }
  
  .header-buttons {
    width: 100%;
    justify-content: space-between;
    gap: 6px;
  }
  
  .header-buttons button {
    padding: 6px 10px;
    font-size: 0.8rem;
    flex: 1;
  }
  
  .date-selector-wrapper,
  .day-selector-wrapper {
    margin-right: 0;
  }
  
  .berth-planner {
    min-width: 800px; /* Giảm min-width trên mobile */
  }
}

/* Màn hình rất nhỏ */
@media (max-width: 480px) {
  .app-header h1 {
    font-size: 0.9rem;
  }
  
  .header-buttons button {
    padding: 4px 8px;
    font-size: 0.75rem;
  }
  
  .berth-planner {
    min-width: 600px;
  }
  
  .date-input {
    width: 120px;
    font-size: 0.75rem;
  }
  
  .day-selector-wrapper select {
    font-size: 0.8rem;
    padding: 4px 8px;
  }
}
